ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

.container {
    display: flex;
}

.second_container {
    padding-top: 50px;
    gap: 20px;
    display: flex;
    flex-direction: column;
}

.movie_pic {
    margin: 10px 10px 20px 10px;
}

.title {
    margin-left: 40px;
    font-size: 35px;
}

.user_score {
    margin-left: 20px;
    font-size: 20px;
}

.overview {
    margin-left: 40px;
    font-size: 25px;
}

.overview_text {
    margin-left: 20px;
    size: 16px;
    line-height: 25px;
}

.genres {
    margin-left: 40px;
    font-size: 25px;
}

.genres_list {
    margin-left: 20px;
}

.genres_padding_list {
    padding-bottom: 5px;
}

.button {
    background-color: #3f3633;
    border-radius: 5px;
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    font-size: 16px;
    margin: 4px 2px;
    opacity: 0.6;
    transition: 0.3s;
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
}

.button:hover {
    opacity: 1;
}

.option {
    display: flex;
    justify-content: space-around;
}

.link {
    text-decoration: none;
    color: black;
    font-size: 25px;
    text-align: center;
    
}
